<template>
  <div>
    <header class="mui-bar mui-bar-nav">
      <button class="mui-action-back mui-btn mui-btn-blue mui-btn-link mui-btn-nav mui-pull-left" @click="goBack">
        <span class="mui-icon mui-icon-left-nav"></span>返回
      </button>
      <h1 class="mui-title">保单信息</h1>
      <div class="headerNews">
        <a class="mui-icon mui-icon-bars mui-pull-right"></a>
        <span></span>
      </div>
    </header>
    <div class="mui-content">
      <div class="policyList policySee wb100">
        <ul v-for="(item,index) in dataList" class="wb100">
          <li>
            <div class="liHeader" @click="changeClass('base')">
              <img :class="plyBase?'zk arrowBottom':'zk'" :src="icon_zk" alt>
              <em>基本信息</em>
            </div>
            <div class="wb100 listBox" v-show="plyBase">
              <ul  class="listText">
                <li>
                  <span>保单号</span>
                  <span>{{item.policyQueryBaseVo.cPlyNo}}</span>
                </li>
                <li>
                  <span>承保机构</span>
                  <span>{{item.policyQueryBaseVo.cDptCName}}</span>
                </li>
                <li>
                  <span>承包机构编码</span>
                  <span>{{item.policyQueryBaseVo.cDptCde}}</span>
                </li>
                <li>
