                  <span>{{item.policyQueryBaseVo.tInsrncEndTm}}</span>
                </li>
                <li>
                  <span>总保额</span>
                  <span>{{item.policyQueryBaseVo.nAmt}}</span>
                </li>
                <li>
                  <span>总保费</span>
                  <span>{{item.policyQueryBaseVo.nPrm}}</span>
                </li>
                <li>
                  <span>车主</span>
                  <span></span>
                </li>
                <li>
                  <span>车牌号</span>
                  <span></span>
                </li>
                <li>
                  <span>车架号</span>
                  <span></span>
                </li>
                <li>
                  <span>发动机号</span>
                  <span></span>
                </li>
                <li>
                  <span>初登日期</span>
                  <span></span>
                </li>
                <li>
                  <span>使用年限</span>
                  <span></span>
                </li>
                <li>
                  <span>业务员名称</span>
                  <span>{{item.policyQueryBaseVo.cProdName}}</span>
                </li>
                <li>
                  <span>业务员电话</span>
                  <span>{{item.policyQueryBaseVo.opertel}}</span>
                </li>
              </ul>
            </div>
          </li>

          <li>
            <div class="liHeader" @click="changeClass('cvrg')">
              <img :class="cvrg?'zk arrowBottom':'zk'" :src="icon_zk" alt>
              <em>险别信息</em>
            </div>
            <div class="wb100 listBox" v-show="cvrg">
              <ul v-for="(cvrg,index) in item.policyQueryCvrgVOList" class="listText">
                <li>
                  <em><em></em>{{cvrg.cCvrgName}}</em>
                </li>
                <li>
                  <span>险别代码</span>
                  <span>{{cvrg.cCvrgCode}}</span>
                </li>
                <li>
                  <span>险别名称</span>
                  <span>{{cvrg.cCvrgName}}</span>
                </li>
                <li>
                  <span>责任代码</span>
                  <span>{{cvrg.cLiabCode}}</span>
                </li>
                <li>
                  <span>责任名称</span>
                  <span>{{cvrg.cLiabName}}</span>
                </li>
                <li>
                  <span>保额</span>
                  <span>{{cvrg.nAmt}}</span>
                </li>
                <li>
                  <span>保费</span>
                  <span>{{cvrg.nPrm}}</span>
                </li>
              </ul>
            </div>
          </li>

          <li>
            <div class="liHeader">
              <img class="zk" :src="icon_zk" alt>
              <em>标的信息</em>
            </div>
            <div class="wb100 listBox" style="display:none;">
              <ul class="listText">
                <li>
                  <span>保单号</span>
                  <span>zlbd0000111111</span>
                </li>
                <li>
                  <span>被保险人</span>
                  <span>张三</span>
                </li>
                <li>
                  <span>承保机构</span>
                  <span>中路保险</span>
                </li>
                <li>
                  <span>承包机构编码</span>
                  <span>1234567890</span>
                </li>
                <li>
                  <span>起保日期</span>
                  <span>2017-10-10 11:12:00</span>
                </li>
                <li>
                  <span>终保日期</span>
                  <span>2018-10-10 11:12:00</span>
                </li>
                <li>
                  <span>车主</span>
                  <span>李四</span>
                </li>
                <li>
                  <span>车牌号</span>
                  <span>鲁12345</span>
                </li>
                <li>
                  <span>车架号</span>
                  <span>1234567890</span>
                </li>
                <li>
                  <span>发动机号</span>
                  <span>1234567890</span>
                </li>
                <li>
                  <span>初登日期</span>
                  <span>2018-10-10 11:12:00</span>
                </li>
                <li>
                  <span>使用年限</span>
                  <span>3</span>
                </li>
                <li>
                  <span>业务员名称</span>
                  <span>李四</span>
                </li>
                <li>
                  <span>业务员电话</span>
                  <span>18723543267</span>
                </li>
              </ul>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import zk from './source/zk.png';
export default {
    data() {
        return{
            policyNo: this.$route.params.policyNo,
            type: this.$route.query.type,
            icon_zk: zk,
            plyBase: true,
            cvrg: false,
            appInfo: false,
            insInfo: false,
            dataList:[]
        }
    },
    mounted(){
        this.initPolicyInfo();
    },
    methods: {
        initPolicyInfo: function() {
        axios({
                method: "post",
                url: "NonVehiclePolicyList/policyInfoDetails",
                params: {
                    plyNo: this.policyNo,
                    type:this.type
                }
            })
            .then(response => {
                this.dataList = response.data;
            })
            .catch(error => {
                alert(error);
            });
        },
        changeClass: function(type) {
          if(type == 'base'){
           this.plyBase = !this.plyBase;
          }else if(type == 'cvrg'){
            this.cvrg = !this.cvrg;
          }
        },
        goBack: function() {
          this.$router.back(-1);
        }
    }
}
</script>
<style>
@import '../../assets/MUI/css/style.css';
</style>



